import { formatTime } from "../helpers/helpers";
import Lap from "./Lap";

const LapRunner = ({ pos, laps, addLap }) => (
  <div>
    <label>Runner {pos}</label>
    {/* <h2>{formatTime(time)}</h2> */}
    <button onClick={addLap}>Add Lap</button>
    <button>End Run</button>
    {laps.map((lap, i) => {
      const lapNumber = laps.length - 1;
      // label={`Lap #${lapNumber}`} time={lap}
      //
      return (
        <div key={lapNumber}>
          <label className="lap__label">{`Lap #${lapNumber}`}</label>
          <h3 className="lap__clock">{formatTime(lap)}</h3>
        </div>
      );
    })}
  </div>
);

export default LapRunner;
